* {
  margin: 0;
  padding: 0;
  border: none;
}

body {
  background: #000;
}

h1,
a {
  text-align: center;
  color: #fff;
  display: block;
  text-decoration: none;
  font-size: 3rem;
  font-family: 'Fruktur', cursive;
  text-shadow: 0.0625rem  0.0625rem 0.0625rem #ccc,
                  0 0 0.625rem #fff,
                   0 0 1.25rem #fff,
                   0 0 1.875rem #fff,
                   0 0 2.5rem #ff00de,
                   0 0 4.375rem #ff00de,
                   0 0 5rem #ff00de,
                   0 0 6.25rem #ff00de,
                   0 0 9.375rem #ff00de;
  transform-style: preserve-3d;
  -moz-transform-style: preserve-3d;
  -webkit-transform-style: preserve-3d;
  -ms-transform-style: preserve-3d;
  -o-transform-style: preserve-3d;
  animation: run  ease-in-out 9s infinite;
  -moz-animation: run  ease-in-out 9s infinite;
  -webkit-animation: run  ease-in-out 9s infinite;
  -ms-animation: run  ease-in-out 9s infinite;
  -o-animation: run  ease-in-out 9s infinite;
}

@keyframes run {
  0% {
    transform: rotateX(-5deg) rotateY(0);
  }

  50% {
    transform: rotateX(0) rotateY(180deg);
    text-shadow: 0.0625rem  0.0625rem 0.0625rem #ccc,
                  0 0 0.625rem #fff,
                   0 0 1.25rem #fff,
                   0 0 1.875rem #fff,
                   0 0 2.5rem #3EFF3E,
                   0 0 4.375rem #3EFFff,
                   0 0 5rem #3EFFff,
                   0 0 6.25rem #3EFFee,
                   0 0 9.375rem #3EFFee;
  }

  100% {
    transform: rotateX(5deg) rotateY(360deg);
  }
}

#content {
  margin-top: 20%;
}


